// COLORS

  $color-black: #000000;
  $color-white: #ffffff;
  $color-hover: #4784b6;
  $color-favorit: #151515;

//DIMENSIONS
  $small: 15px;
  $medium: 30px;
  $large: 70px;
  $transition:  .4s;

// FONTS

  $font-book: 'Noto Sans', sans-serif;
  $font-regular: 'Nunito', sans-serif;
  
  $book-low: 400 18px / 130% $font-book;  
  $book-medium: 400 24px / 130% $font-book; 
  $book-pre-high: 700 36px / 130% $font-book; 
  $book-high: 700 64px / 100% $font-book; 

  $lato: 400 18px / 100% $font-regular; 
